import React, {PureComponent} from "react"
import {Collapse, Typography} from 'antd'

const {Panel} = Collapse
const {Paragraph} = Typography

class DrupalWorks extends PureComponent {
    render() {
        return (
            <div>
                <h2>Drupal Projects</h2>
                <Collapse defaultActiveKey={['1']} accordion>
                    <Panel key='1' header='Drupal Websites'>
                        <Paragraph>Some of Drupal based websites that I've developed and currently are active<br/>
                            <a href='http://itrc.ac.ir'>http://itrc.ac.ir</a><br/>
                            <a href='http://hollymatic.com'>http://hollymatic.com</a><br/>
                            <a href='http://www.hollymaticparts.com'>http://www.hollymaticparts.com</a><br/>
                            <a href='http://tajmirexchange.com'>http://tajmirexchange.com</a>
                        </Paragraph>
                    </Panel>
                    <Panel key='2' header='Commerce Marketplace MangoPay'>
                        <Paragraph>
                            This module fully integrates Commerce Marketplace Funds with MangoPay Payments.
                            It supports deposit, transfer, payment and withdraw transactions.<br/>
                            MangoPay transactions are duplicated on Drupal database for making things
                            faster, but queries are made to MangoPay to validate local data before doing
                            sensitive operations like transfers or payments.
                        </Paragraph>
                        <a href='https://www.drupal.org/sandbox/farhadhf/2072127'>Read more ...</a>
                    </Panel>
                    <Panel key='3' header='Flash Game'>
                        <Paragraph>
                            Do you want to develop a flash game website using drupal ?<br/>
                            Do you want to upload swf files and games in your website and show them az embed object
                            ?<br/>
                            Do you want to add miniclip.com games to your website ?<br/>
                            If the answer is yes so you sholud use this module<br/>
                            This module provides a formatter for file field , so you can upload swf files into your
                            website and show them as a embed object by this formatter<br/>
                            The second job of this module is to provide a easy system for adding miniclip.com games into
                            your website just by copy pasting the url of the game page<br/>
                            <hr/>
                            Currently 35 sites report using this module
                        </Paragraph>
                        <a href='https://www.drupal.org/project/flashgame'>Read more ...</a>
                    </Panel>
                    <Panel key='4' header='Video Embed Modules'>
                        <Paragraph>
                            These modules let you use videos from Dailymotion, Metacafe, Myspace and Rutube in your
                            Drupal websites<br/>
                            <hr/>
                            Currently more than 1500 sites report using these modules
                        </Paragraph>
                        <a href='https://www.drupal.org/project/video_embed_dailymotion'>Video Embed
                            Dailymotion</a><br/>
                        <a href='https://www.drupal.org/project/video_embed_metacafe'>Video Embed Metacafe</a><br/>
                        <a href='https://www.drupal.org/project/video_embed_myspace'>Video Embed MySpace</a><br/>
                        <a href='https://www.drupal.org/project/video_embed_rutube'>Video Embed Rutube</a><br/>
                    </Panel>
                    <Panel key='5' header='Commerce Marketplace'>
                        <Paragraph>
                            This projects is a set of modules which lets you create a marketplace using Drupal Commerce.<br/>
                            <hr/>
                            Currently 162 sites report using this module
                        </Paragraph>
                        <a href='https://www.drupal.org/project/commerce_marketplace'>Read more ...</a>
                    </Panel>

                </Collapse>
            </div>
        )
    }
}

export default DrupalWorks