import React, {PureComponent} from "react"
import {Carousel, Card, Icon, Typography, PageHeader} from "antd"

const {Meta} = Card
const {Paragraph} = Typography

class UnityWorks extends PureComponent {
    render() {
        return (
            <PageHeader
                title='Unity Project'>
                <Card style={{maxWidth: '850px', margin: 'auto'}} hoverable
                      cover={
                          <Carousel autoplay>
                              <img src='df1.jpg'/>
                              <img src='df2.jpg'/>
                              <img src='df3.jpg'/>
                              <img src='df4.jpg'/>
                              <img src='df5.jpg'/>
                              <img src='df6.jpg'/>
                              <img src='df7.jpg'/>
                          </Carousel>
                      }

                      actions={[
                          <a href='https://cafebazaar.ir/app/com.pouryazdan.darknessfights'>
                              <Icon type="link" key="link"/>
                          </a>
                      ]}
                >
                    <Meta
                        title="Darkness Fights"
                        description={
                            <Paragraph>
                                Darkness fights is an Iranian online multi-player game with some special features<br/>
                                Fight versus other players with powerful servers in Iran<br/>
                                Beautiful Graphics<br/>
                                Less than 34 megabytes without any extra data<br/>
                                Tweaked to let players play the game on every device that runs android 5 or higher<br/>
                                You can earn free gems by winning games or watching rewarded ads<br/>
                                You can go high in rank list and become one of the best players by winning ranked
                                matches<br/>
                                You can play games without any changes on your ranks by playing in Free Fight Mode<br/>
                                2 free heroes for everyone
                            </Paragraph>
                        }
                    />
                </Card>
            </PageHeader>
        )
    }
}

export default UnityWorks