import React, {PureComponent} from 'react'
import {Icon, PageHeader} from "antd"
import {ReactComponent as FacebookLogo} from "../logos/facebook.svg"
import {ReactComponent as InstagramLogo} from "../logos/instagram.svg"
import {ReactComponent as LinkedinLogo} from "../logos/linkedin.svg"
import {ReactComponent as TelegramLogo} from "../logos/telegram.svg"
import {ReactComponent as TwitterLogo} from "../logos/twitter.svg"


class Contact extends PureComponent {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <PageHeader
                title='Contact'>
                You can always reach me using this email address or the social medias listed below :&nbsp;
                <a href='mailto:mohs3n71@gmail.com'>mohs3n71@gmail.com</a>
                <div className="icons-list" style={{fontSize: '50px'}}>
                    <a href='https://facebook.com/mohs3n71'><Icon component={FacebookLogo}/></a>
                    <a href='https://instagram.com/mohs3n71'><Icon component={InstagramLogo}/></a>
                    <a href='https://linkedin.com/in/mohs3n71'><Icon component={LinkedinLogo}/></a>
                    <a href='https://t.me/mohs3n71'><Icon component={TelegramLogo}/></a>
                    <a href='https://twitter.com/mohs3n71'><Icon component={TwitterLogo}/></a>
                </div>
            </PageHeader>
        )
    }
}

export default Contact

