import React, {PureComponent} from "react"
import {Row, Col, Carousel, Card, Typography, Icon} from "antd"
import './androidWorks.css'

const {Paragraph} = Typography
const {Meta} = Card

class AndroidWorks extends PureComponent {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div>
                <h2>Android Projects</h2>
                <Row>
                    <Col xs={24} sm={24} md={24} lg={24} xl={12} style={{textAlign: 'center'}}>
                        <Card style={{maxWidth: '350px', margin: 'auto'}} hoverable
                              cover={
                                  <Carousel autoplay>
                                      <img src='medopad1.jpg'/>
                                      <img src='medopad2.jpg'/>
                                      <img src='medopad3.jpg'/>
                                  </Carousel>
                              }

                              actions={[
                                  <a href='https://play.google.com/store/apps/details?id=com.medopad.monitoring'><Icon
                                      type="link" key="link"/></a>
                              ]}
                        >
                            <Meta
                                title="Medopad"
                                description={
                                    <Paragraph>
                                        Medopad is your personal health companion app.The app tracks your health between
                                        doctor's appointments and shares your health information so that you and your
                                        care team
                                        can make better decisions. Your doctor has to recommend the app to you for you
                                        to be
                                        eligible to start using the app.
                                    </Paragraph>
                                }
                            />
                        </Card>
                    </Col>
                    <Col xs={24} sm={24} md={24} lg={24} xl={12} style={{textAlign: 'center'}}>
                        <Card style={{maxWidth: '350px', margin: 'auto'}} hoverable
                              cover={
                                  <Carousel autoplay>
                                      <img src='tanfit1.jpg'/>
                                      <img src='tanfit2.jpg'/>
                                      <img src='tanfit3.jpg'/>
                                      <img src='tanfit4.jpg'/>
                                      <img src='tanfit5.jpg'/>
                                  </Carousel>
                              }

                              actions={[
                                  <a href='https://cafebazaar.ir/app/com.pouryazdan.mohsen.tanfit'>
                                      <Icon type="link" key="link"/>
                                  </a>
                              ]}
                        >
                            <Meta
                                title="Tanfit"
                                description={
                                    <Paragraph>
                                        TanFit is a free application to let users exercise in home without any
                                        instrument
                                        TanFit includes three sections : High-intensity interval trainings, Abs Workout,
                                        Bootcamp
                                        every section consists of three level : easy, medium, hard
                                    </Paragraph>
                                }
                            />
                        </Card>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default AndroidWorks