import React, {PureComponent} from 'react'
import {Descriptions} from "antd"

class Education extends PureComponent {

    render() {
        return (
            <div>
                <h2>Educations</h2>
                <Descriptions size='small' column={2}>
                    <Descriptions.Item label='Course'>Bachelor of Computer Science</Descriptions.Item>
                    <Descriptions.Item label='University'>Amirkabir University of Technology</Descriptions.Item>
                    <Descriptions.Item label='Start'>2010</Descriptions.Item>
                    <Descriptions.Item label='End'>2015</Descriptions.Item>
                </Descriptions>
            </div>
        )
    }
}

export default Education