import React, {PureComponent} from 'react'
import {PageHeader, Typography, Tag, Descriptions, Row, Col} from 'antd'

import './about.css'

const {Paragraph} = Typography

class About extends PureComponent {
    render() {
        return (
            <PageHeader
                title='Mohsen Pouryazdan'
                subTitle='Full stack Developer'
                avatar={{src: 'profile.jpg'}}>
                <Descriptions size='default'>
                    <Descriptions.Item label='Name'>Mohsen</Descriptions.Item>
                    <Descriptions.Item label='Lastname'>Pouryazdan</Descriptions.Item>
                    <Descriptions.Item label='Birthday'>July 6 1992</Descriptions.Item>
                </Descriptions>
                <Paragraph>
                    My name is Mohsen Pouryazdan.
                    I'm a Full-stack developer living in Tehran.I started programming back in 2008.<br/>
                    I use <Tag color='#B0B3D6'>PHP</Tag>for developing back-end, <Tag color='#61dbfb'>React</Tag>for
                    Front-end, <Tag color='#f89820'>Java</Tag>for developing <Tag color='#3DDC84'>Android</Tag>applications
                    and <Tag color='green'>C#</Tag>for developing games in <Tag color='black'>Unity</Tag><br/>
                    I have also experience in developing <Tag color='#41cd52'>Qt</Tag>applications and a very very good
                    understanding of <Tag color='red'>C</Tag>and <Tag color='blue'>C++</Tag>languages.
                    I have also developed lots of <Tag color='#0073ba'>drupal</Tag>modules.<br/><br/>
                    Me in one sentence ? Always ready to learn.<br/><br/>
                    <b>Currently I'm obsessed with Javascript and React.js so I'm only looking for React.js jobs at the
                        moment and I don't accept job offers in other fields</b>
                </Paragraph>

                <Row>
                    <Col xs={24} sm={24} md={24} lg={12} xl={8}>
                        <h3><b>Javascript Knowledge</b></h3>
                        <ul>
                            <li>Function binding</li>
                            <li>Hoisting</li>
                            <li>Lexical scope and Closure</li>
                            <li>Block scopes</li>
                            <li>Arrow functions</li>
                            <li>Generators</li>
                            <li>Iterables and Iterators</li>
                            <li>Promises, async functions and await</li>
                            <li>Template literals and Tagged template literals</li>
                            <li>Javascript Classes</li>
                            <li>ES 6 modules</li>
                        </ul>
                    </Col>
                    <Col xs={24} sm={24} md={24} lg={12} xl={8}>
                        <h3><b>React.js Knowledge</b></h3>
                        <ul>
                            <li>Virtual Dom and Reconciliation</li>
                            <li>JSX</li>
                            <li>Components, Props, States and Lifecycle</li>
                            <li>Context</li>
                            <li>Refs</li>
                            <li>Fragments</li>
                            <li>Portals</li>
                            <li>Static Type checking</li>
                            <li>Hooks, Render Props, Higher-order components</li>
                            <li>How to improve performance (Stopping unnecessary re-renders using PureComponents,
                                shouldComponentUpdate and also using redux to do the render only in leafs of the tree)
                            </li>
                        </ul>
                    </Col>
                    <Col xs={24} sm={24} md={24} lg={12} xl={8}>
                        <h3><b>Third-party libraries and tools that I use along side React</b></h3>
                        <ul>
                            <li>Ant Design</li>
                            <li>Axios</li>
                            <li>Babel</li>
                            <li>Lodash</li>
                            <li>Redux</li>
                            <li>Redux-Saga as Redux middleware</li>
                            <li>Styled Components</li>
                            <li>Yarn</li>
                        </ul>
                    </Col>
                </Row>
                <b>And I should add as much as I love using React in web applications I hate using React-Native for creating mobile applications</b>
            </PageHeader>
        )
    }
}

export default About