import {Layout, Menu, Icon} from 'antd'
import React, {PureComponent} from 'react'
import {isMobile} from 'react-device-detect'

import './App.css'
import {ReactComponent as AndroidLogo} from "./logos/android.svg"
import {ReactComponent as DrupalLogo} from "./logos/drupal.svg"
import {ReactComponent as ReactLogo} from "./logos/logo.svg"
import {ReactComponent as UnityLogo} from './logos/unity.svg'
import {ReactComponent as UserLogo} from './logos/user.svg'
import {ReactComponent as WorksLogo} from "./logos/workspace.svg"
import {ReactComponent as EducationLogo} from './logos/books.svg'
import {ReactComponent as EmailLogo} from './logos/email.svg'
import {ReactComponent as ExperiencesLogo} from './logos/experience.svg'

import About from './components/about'
import Education from './components/education'
import Contact from "./components/contact"
import AndroidWorks from "./components/androidWorks"
import Experiences from "./components/experiences"
import UnityWorks from "./components/unityWorks"
import DrupalWorks from "./components/drupalWorks"
import ReactWorks from "./components/reactWorks"

const {Header, Sider, Content, Footer} = Layout
const {SubMenu} = Menu

class App extends PureComponent {

    constructor(props) {
        super(props)

        this.state = {
            collapsed: false,
            content: 'about'
        }

        this.__showContent = this.__showContent.bind(this)
    }

    componentDidMount() {
        if (isMobile) {
            this.setState({collapsed: true});
        }
    }

    __showContent() {
        switch (this.state.content) {
            case 'education':
                return <Education/>
            case 'contact':
                return <Contact/>
            case 'android':
                return <AndroidWorks />
            case 'experiences':
                return <Experiences />
            case 'unity':
                return <UnityWorks />
            case 'drupal':
                return <DrupalWorks />
            case 'react':
                return <ReactWorks />
            default:
                return <About />
        }
    }


    toggle = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        })
    }


    render() {
        const iconStyle = {
            fontSize: '33px',
            verticalAlign: 'middle'
        }

        return (
            <Layout>
                <Sider trigger={null} collapsible collapsed={this.state.collapsed}>
                    <div className="logo">
                        <img alt='Profile Picture' className='profile' src='profile.jpg'/>
                    </div>
                    <Menu onClick={(e) => {
                        switch (e.key) {
                            case '2':
                                this.setState({content: 'education'})
                                break
                            case '3':
                                this.setState({content: 'android'})
                                break
                            case '4':
                                this.setState({content: 'drupal'})
                                break
                            case '5':
                                this.setState({content: 'react'})
                                break
                            case '6':
                                this.setState({content: 'unity'})
                                break
                            case '7':
                                this.setState({content: 'contact'})
                                break
                            case '8':
                                this.setState({content: 'experiences'})
                                break
                            default:
                                this.setState({content: 'about'})
                        }
                    }}
                          theme="dark" mode="inline" defaultSelectedKeys={['1']}>
                        <Menu.Item key="1">
                            <Icon style={iconStyle} component={UserLogo}/>
                            <span>About me</span>
                        </Menu.Item>
                        <Menu.Item key="2">
                            <Icon style={iconStyle} component={EducationLogo}/>
                            <span>Education</span>
                        </Menu.Item>
                        <Menu.Item key="8">
                            <Icon style={iconStyle} component={ExperiencesLogo}/>
                            <span>Experiences</span>
                        </Menu.Item>
                        <SubMenu
                            key="sub1"
                            title={
                                <span>
                                    <Icon style={iconStyle} component={WorksLogo}/>
                                    <span>Projects</span>
                                </span>}>
                            <Menu.Item key="3">
                                <Icon component={AndroidLogo} style={iconStyle}/>
                                <span>Android</span>
                            </Menu.Item>
                            <Menu.Item key="4">
                                <Icon style={iconStyle} component={DrupalLogo}/>
                                <span>Drupal</span>
                            </Menu.Item>
                            <Menu.Item key="5">
                                <Icon style={iconStyle} component={ReactLogo}/>
                                <span>React</span>
                            </Menu.Item>
                            <Menu.Item key="6">
                                <Icon style={iconStyle} component={UnityLogo}/>
                                <span>Unity</span>
                            </Menu.Item>
                        </SubMenu>
                        <Menu.Item key="7">
                            <Icon style={iconStyle} component={EmailLogo}/>
                            <span>Contact</span>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Layout>
                    <Header
                        style={
                            {
                                background: '#fff',
                                padding: 0
                            }
                        }>
                        <Icon
                            className="trigger"
                            type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                            onClick={this.toggle}
                        />
                    </Header>
                    <Content
                        style={{
                            margin: '24px 16px',
                            padding: 24,
                            background: '#fff',
                            minHeight: '80vh',
                        }}>
                        {this.__showContent()}
                    </Content>
                    <Footer style={{textAlign: 'center'}}>
                        Designed and created by Mohsen Pouryazdan using React.js
                    </Footer>
                </Layout>
            </Layout>
        )
    }
}

export default App