import React, {PureComponent} from "react"
import {Collapse, PageHeader, Typography} from 'antd'

const {Panel} = Collapse
const {Paragraph} = Typography

class ReactWorks extends PureComponent {
    render() {
        return (
            <PageHeader
                title='React.js Projects'>
                <Collapse defaultActiveKey={['1']} accordion>
                    <Panel key='1' header='Medopad Admin console'>
                        <Paragraph>I've developed an admin console for Medopad company ( https://medopad.com/ ) using
                            React and
                            Redux ( Using FLUX structure in React web application )
                        </Paragraph>
                    </Panel>
                    <Panel key='2' header='React-Redux-StarterKit'>
                        <Paragraph>
                            This Starter kit helps you to learn how to use redux in a react application and also you can
                            use
                            it to start a new project fast and easy<br/><br/>
                            I've used Ant.Design(https://ant.design) for UI<br/>
                            Redux-Saga as redux middleware<br/>
                            Axios for Http Requests<br/>
                            Jest for Unit testing<br/>
                            and Lodash (https://lodash.com/) for having a better performance<br/>
                        </Paragraph>
                        <a href='https://github.com/mohs3n71/react-redux-starterkit'>Read more ...</a>
                    </Panel>
                </Collapse>
            </PageHeader>
        )
    }
}

export default ReactWorks