import React, {PureComponent} from "react"
import {Descriptions, Collapse, Typography} from 'antd'

const {Panel} = Collapse
const {Paragraph} = Typography

class Experiences extends PureComponent {
    render() {
        return (
            <div>
                <h2>Experiences</h2>
                <Collapse defaultActiveKey={['1']} accordion>
                    <Panel key='1' header='Shayaweb'>
                        <Descriptions title='Drupal module developer'>
                            <Descriptions.Item label='Start'>March 2013</Descriptions.Item>
                            <Descriptions.Item label='End'>March 2014</Descriptions.Item>
                        </Descriptions>
                    </Panel>
                    <Panel key='2' header='IRAN Telecommunication Research Center'>
                        <Descriptions title='Drupal Developer'>
                            <Descriptions.Item label='Start'>July 2014</Descriptions.Item>
                            <Descriptions.Item label='End'>March 2015</Descriptions.Item>
                        </Descriptions>
                        <Paragraph>
                            Drupal / PHP Leader Programmer<br/>
                            Drupal Module development<br/>
                            Backend Developer
                        </Paragraph>
                    </Panel>
                    <Panel key='3' header='Medopad'>
                        <Descriptions title='Front-end and Android developer'>
                            <Descriptions.Item label='Start'>Jan 2017</Descriptions.Item>
                            <Descriptions.Item label='End'>April 2018</Descriptions.Item>
                        </Descriptions>
                        <Paragraph>
                            I started my work as Android developer in medopad and continued my journey as Front-end web developer
                        </Paragraph>
                    </Panel>

                </Collapse>
            </div>
        )
    }
}

export default Experiences